'use client'

import type React from "react"
import { useRef, useEffect } from "react"
import { Search, ArrowUp } from "lucide-react"

export interface Message {
  role: "user" | "assistant"
  content: string
}

export function MessageList({ messages, isLoading }: { messages: Message[]; isLoading: boolean }) {
  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  return (
    <div className="flex-1 overflow-y-auto mb-4 rounded-lg flex flex-col">
      {messages.map((message, index) => (
        <div key={index} className={`mb-4 ${message.role === "user" ? "text-right" : "text-left"}`}>
          <div className={`inline-block px-4 py-2 rounded-3xl break-words text-left${
            message.role === "user" 
            ? `
              bg-white 
              dark:bg-neutral-950 
              border border-neutral-200 
              dark:border-neutral-800
              text-neutral-500
              dark:text-neutral-400
              font-medium
              max-w-[80%]
              `
            : `
              text-neutral-700
              dark:text-neutral-300
              font-medium
              max-w-[100%]
              `
            }`}>
            {message.content}
          </div>
        </div>
      ))}
      {isLoading && (
        <div className="text-left mb-4">
          <div className="inline-block px-4 py-2 rounded-lg text-neutral-800 dark:text-neutral-100">
            <div className="flex space-x-1">
              <div className="w-2 h-2 bg-neutral-500 dark:bg-neutral-400 rounded-full animate-bounce"></div>
              <div className="w-2 h-2 bg-neutral-500 dark:bg-neutral-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
              <div className="w-2 h-2 bg-neutral-500 dark:bg-neutral-400 rounded-full animate-bounce" style={{ animationDelay: "0.4s" }}></div>
            </div>
          </div>
        </div>
      )}
      <div ref={messagesEndRef} />
    </div>
  )
}

export function ChatInput({ input, setInput, isLoading, useWebSearch, setUseWebSearch, onSubmit }: any) {
  return (
    <form onSubmit={onSubmit} className="w-full max-w-4xl">
      <div className="
      bg-white 
      dark:bg-neutral-950 
      border border-neutral-200 
      dark:border-neutral-800 
      p-2
      rounded-3xl
      shadow-lg mt-4">
        <textarea
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type your message..."
          disabled={isLoading}
          rows={1}
          className="
            w-full
            resize-none
            overflow-hidden
            p-2
            font-medium
            focus:outline-none
            text-neutral-400
            bg-transparent
          "
          onInput={(e) => {
            e.currentTarget.style.height = "auto"
            e.currentTarget.style.height = e.currentTarget.scrollHeight + "px"
          }}
        />
        <div className="flex items-center gap-2 justify-between w-full">
          <button
            type="button"
            onClick={() => setUseWebSearch(!useWebSearch)}
            className={`flex items-center gap-1 p-2 rounded-full ${
              useWebSearch
                ? `
                  border border-blue-200 
                  dark:border-blue-900 
                  bg-blue-50 
                  dark:bg-blue-950 
                  text-neutral-400 
                  hover:bg-blue-100 
                  dark:hover:bg-blue-900
                  `
                : `
                  border border-neutral-200 
                  dark:border-neutral-700
                  text-neutral-400 
                  hover:bg-neutral-100 
                  dark:hover:bg-neutral-800
                  `
            }`}
          >
            <Search size={20} />
            <span className="text-sm font-semibold">Search</span>
          </button>

          {input.trim() && !isLoading && (
            <button
              type="submit"
              className="
              p-2 rounded-full 
              border border-lime-500 
              dark:border-lime-600 
              bg-lime-400 
              dark:bg-lime-500 
              text-white 
              hover:bg-lime-500 d
              ark:hover:bg-lime-600
              "
            >
              <ArrowUp size={20} />
            </button>
          )}
        </div>
      </div>
    </form>
  )
}